import styled from "styled-components";
export const PokemonListContainer = styled.div`
  max-height: calc(100vh - 80px); /* Ajuste para no ocupar a altura total */
  overflow-y: auto; /* Garante que o contedo seja rolvel */
  margin: 10px;
  padding: 10px;
  display: flex;
  flex-direction: column;
  height: 100vh;

  /* Esconder a scrollbar no navegador */
  scrollbar-width: none;
  -ms-overflow-style: none;

  &::-webkit-scrollbar {
    display: none;
  }
`;
